export const randomId = (isPassword: boolean = false): string => {
    let s4 = () => {
        return Math.floor((1 + Math.random()) * 0x10000)
            .toString(16)
            .substring(1);
    };
    if (!isPassword) return `${s4()}${s4()}-${s4()}`;
    return `${s4()}${s4()}-${s4()}${s4()}-${s4()}${s4()}-${s4()}${s4()}-`;
};
